@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes text {
  0% {
    opacity: 0;
    letter-spacing: 150px;
    text-shadow: 0 0 50px #fff;
  }
  50% {
    letter-spacing: 50px;
  }
  100% {
    opacity: 0.8;
    letter-spacing: 3px;
    text-shadow: 0 0 1px #fff;
  }
}

//Mixins
@mixin basic($height, $width) {
  height: $height;
  width: $width;
}

@mixin indents($margin, $padding) {
  margin: $margin;
  padding: $padding;
}
@mixin position($position, $top, $left) {
  position: $position;
  top: $top;
  left: $left;
}
@mixin position1($position, $bottom, $left) {
  position: $position;
  bottom: $bottom;
  left: $left;
}

@mixin dis-flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
